import { createSlice } from "@reduxjs/toolkit";

export const generateSlice = createSlice({
  name: "generate",
  initialState: {
    radio: "3",
    strategies: ["lowercase", "uppercase"],
    length: "12",
    reliabilityColor: "grey",
    password: "",
  },
  reducers: {
    setRadio: (state, action) => {
      state.radio = action.payload;
    },
    setPasswordLength: (state, action) => {
      state.length = action.payload;
    },
    setStrategies: (state, action) => {
      state.strategies = action.payload;
    },
    setPassword: (state, action) => {
      state.password = action.payload;
    },
  },
});

export const { setRadio, setPasswordLength, setStrategies, setPassword } =
  generateSlice.actions;
export default generateSlice.reducer;
